.message {
  .message_content {
    display: flex;
    margin-bottom: 30px;
  }
  .message_avatar {
    display: flex;
    align-items: flex-end;
    margin-bottom: 5px;
    margin-right: 5px;
    img {
      width: 25px;
      height: 25px;
      border-radius: 50%;
    }
  }
  .message_info {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 500px;
    .message_buble {
      background: chocolate;
      border-radius: 55px 55px 55px 0px;
      .message_text {
        padding: 15px 10px 0 20px;
      }
    }
    .message_date {
      position: absolute;
      bottom: -25px;
    }
  }

  &--isMe {
    .message_content {
      justify-content: flex-start;
      flex-direction: row-reverse;
    }
    .message_info {
      .message_buble {
        background: rgb(243, 202, 187);
        border-radius: 55px 55px 0px 55px;
        .message_text {
          padding: 15px 10px 0 20px;
        }
      }
      .message_date {
        position: absolute;
        bottom: -25px;
        right: 0;
      }
    }
    .message_avatar {
      margin-left: 5px;
    }
  }
}
